:root {
  --textColor: #7c4fb3;
}

.App {
  text-align: center;
}


.tab-normal {
  color: #7c4fb3;
  color: var(--textColor);  
  font-size: 20px;
  padding: 8px 16px;
  font-family: Righteous, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.tab-selected {
  background: #7c4fb3;
  background: var(--textColor);
  color: #ffffffcd;
}

.tab {
  cursor: pointer;
}

.tab:hover {
  background-color: #7c4fb366;
  color: #7c4fb3;
}

.title_container{ 
  background-color: #7c4fb3;
  padding: 8px;
}

.title {
  font-family: Righteous, sans-serif, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: inline-block;
  font-size: 12px;
  color: #ffffff;
}

.simple_input {
  border: none;
  display: inline-block;
  font-family: comfortaa, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: transparent;
  color: white;
  font-size: 12px;
}

ul {
  list-style-type: none;
  list-style-position: outside;
  margin: 0;
  padding: 0;
}

ul li {
  display: flex;
  justify-content: space-between;
  padding: 8px;
  align-items: stretch;
}

.label{
  display: inline-block;
  font-family: comfortaa;
  color: #7c4fb3; 
  font-weight: bold;
  font-size: 12px;
}

.simple-input-option {
  display: inline-block;
  width: 100%;
  background: #00000012;
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
  font-family: comfortaa, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 12px;
  flex: 1 1;
}

input:focus, textarea {
  outline: none !important;
}

.cfi {
  color: transparent;
}

.cfi::-webkit-file-upload-button {
  display: none;
}

.cfi::before {
  content: "Select Some files";
  display: inline-block;
  background-color: #7c4fb3;
  border: none;
  border-radius: 2px;
  padding: 8px 16px;
  color: #d1d2d3;
  font-family: Comfortaa;
  font-size: 14px;
  cursor: pointer;
  -webkit-user-select: none;
}

.file-name {
  background: #7c4fb31f;
  border-radius: 4px;
  padding: 8px;
}

