table{
  table-layout: fixed;
}

td, th {
  border: 1px solid #7c4fb31f;
}

.cell-data {
  padding: 4px;
  font-family: "Comfortaa";
  font-size: 12px;
  max-width: 128px;
  overflow: auto;
}

.cell-data::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

.cell-index {
  text-align: center;
  min-width: 24px;
  max-width: 64px;
  font-family: Comfortaa;
  font-size: 10px;
  font-weight: bold;
}

.cell-row:nth-child(2n) {
  background-color: #7c4fb31f;
}

.table-container {
  padding: 8px;
  background: #1212120f;
  height: 75vh;
  margin: 4px;
  border-radius: 8px;
  overflow-y: scroll;
  overflow-x: scroll;
}

.table-container::-webkit-scrollbar {
  width: 10px;
}

.table-container::-webkit-scrollbar-thumb {
  background-color: #7c4fb3;
  border-radius: 2px;
}

button:focus {outline:0;}

.table-container::-webkit-scrollbar-track {
  background-color: transparent;
}

.props-panel::-webkit-scrollbar {
  width: 4px;
}

.props-panel::-webkit-scrollbar-thumb {
  background-color: #7c4fb38f;
  border-radius: 2px;
}

.props-panel::-webkit-scrollbar-track {
  background-color: transparent;
}

.footer-container {
  background-color: #309c3f0f;
  padding-top: 16px;
  padding-bottom: 16px;
  text-align: center;
}

.footer-text {
  color: #7c4fb3bf;
  font-weight: bold;
  display: inline-block;
  padding: 0px;
  margin: 0px;
  font-family: comfortaa;
  font-size: 12px;
}

.link-icon {
  display: inline-block;
  -webkit-padding-start: 8px;
          padding-inline-start: 8px;
  font-size: 16px;
  color: #7c4fb3bf;
  text-decoration: none;
  font-weight: bold;
}

.link-icon:hover{
  color: #7c4fb37f;
  text-decoration: none;
}

.data-art {
  font-size: 72px;
  color: #7c4fb37f;
  font-family: Comfortaa;
  text-align: center;
}

.PointContainer {
 display: inline-block; 
}

@-webkit-keyframes Grow {
  from {top: 100%}
  to {top: 0%}
}

@keyframes Grow {
  from {top: 100%}
  to {top: 0%}
}

.button-play {
  border: none;
  border-radius: 100px;
  padding: 10px 14px 10px 16px;
  color: #7c4fb3bf;
  cursor: pointer;
  position: relative;
}

.button-play:hover {
  color: #7c4fb38f;
}

.reset-btn {
  margin-left: 24px;
  font-size: 20px;
  border: none;
  text-transform: uppercase;
  background-color: transparent;
  font-family: "Righteous";
  color: #7c4fb3;
}

.reset-btn:hover {
  color: #7c4fb38f;
}

::selection {
  color: none;
  background: none;
}
